  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: #ffffff;
  width: 18rem;
  top: 0;
  left: 0;
  height: 100%; }
  @media (max-width: 1399px) {
    .MainSidebar {
      width: 15rem; } }
  @media (max-width: 1023px) {
    .MainSidebar {
      -webkit-transform: translate(-100%);
              transform: translate(-100%); } }
  .MainSidebar .logo {
    font-family: 'Sanchez', serif;
    font-size: 2rem;
    padding: 3rem 2rem; }
  .MainSidebar .menu {
