#PostCardList .post-card-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center; }
  @media (max-width: 767px) {
    #PostCardList .post-card-list {
      -webkit-flex-direction: column;
              flex-direction: column; } }

.PostCard {
  background-color: #ffffff;
  border-radius: 4px;
  width: calc(32.175% - 0.3rem);
  margin: 1rem 0.4rem;
  box-shadow: 0 0 25px 1px #c2b8bd; }
  @media (max-width: 1023px) {
    .PostCard {
      width: calc(50% - 0.8rem); } }
  @media (max-width: 767px) {
    .PostCard {
      width: calc(100% / 1.1); } }
  .PostCard .image {
    text-align: center; }
    .PostCard .image img {
      margin-top: -0.5rem;
      width: 94%;
      border-radius: 4px;
      box-shadow: 0px 0.5rem 25px 1px #adb5bd;
      will-change: transform;
      transition: 0.3s ease; }
      .PostCard .image img:hover {
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem); }
  .PostCard .card-image {
    display: block;
    padding: 10px; }
    .PostCard .card-image .image {
      margin-top: -1rem;
      border-radius: 4px;
      box-shadow: 0px 0.5rem 25px 1px #adb5bd;
      will-change: transform;
      transition: 0.3s ease;
      padding-top: 50%;
      background-size: cover; }
      .PostCard .card-image .image:hover {
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem); }
  .PostCard .title {
    padding: 1.2rem;
    min-height: 3rem;
    overflow: hidden;
    text-overflow: ellipsis; }
  .PostCard .buttons {
    width: 100%;
    text-align: right; }
    .PostCard .buttons span {
      font-size: 1.5rem;
      padding-left: 0.5rem;
      padding-bottom: 0.5rem; }
    .PostCard .buttons span:nth-child(1) {
      padding-left: 0; }
    .PostCard .buttons span:nth-last-child(1) {
      padding-right: 1rem; }

